

































import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class UiInput extends Vue {
    @Prop() placeholder: string

    @Prop() icon: string

    @Prop() errorText: string

    @Prop() value: string

    @Prop() label: string

    @Prop() disabled: string

    @Prop() hits: string

    hitsActive: boolean = false

    onInput($event: any) {
        this.$emit('input', $event.target.value)
    }

    focusInput() {
        this.hitsActive = true
    }

    blurInput() {
        if (this.$refs.input.value.length) {
            this.hitsActive = true
        } else {
            this.hitsActive = false
        }
    }
}
